.BenefitCard {
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
}

.BenefitCard .icon {
    margin-right: 20px;
}

.BenefitCard .textContent .title {
    margin-bottom: 0.5rem !important;
    font-size: 1.1725rem;
    color: var(--primary-color);
    font-weight: 500;
}

.BenefitCard .textContent .text {
    color: #696969;
    margin-bottom: 1rem;
    font-size: .9rem;
}